.card{
    position: relative;
    background-color: white;
    width: 300px;
    padding: 20px;
    max-width: 85vw;
    height: 50vh;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    background-size:cover;
    /* background-repeat: no-repeat; */
    background-position: center;
}

.card_container{
    display: flex;
    justify-content: center;
    margin-top: 5vh;
}

.swipe{
    position: absolute;
}

.card_content{
    width: 100%;
    height: 100%;
}

.card > h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: #fff;
}